.content-area {
  padding: 10px 40px;
  max-width: 1000px;
  margin: 0 auto;
  width: calc(100%-100px)
}

.content-area {
  color: #fff;
}

.light .content-area {
  color: #000;
}

.content-area a {
  color: #ffcc00;
}

.light .content-area a {
  color: #007aff; 
}

.content-area img {
  max-width: 100%;
  box-shadow: 4px 4px 10px rgba(0,0,0,0.5);
  border-radius: 5px;
}

.highlight {
  background-color: #ffcc00;
  color: #000;
  padding: 0px 5px;
}

.content-prev-doc {
  position: absolute;
  width: 50px;
  height: 100%;
  left: 0px;
  top: 0px;

  transition: opacity 0.3s;
  cursor: pointer;

  background-color: rgba(0,0,0,0.2);
  opacity: 0;
  display: flex;

  align-items: center;
  justify-content: center;
}

.content-prev-doc:hover {
  opacity: 1;
}

.content-next-doc {
  position: absolute;
  width: 50px;
  height: 100%;
  right: 0px;
  top: 0px;

  transition: opacity 0.3s;
  cursor: pointer;

  background-color: rgba(0,0,0,0.2);
  opacity: 0;

  display: flex;

  align-items: center;
  justify-content: center;
}

.content-next-doc:hover {
  opacity: 1;
}

@media (max-width: 600px) {
  .content-area {
    padding: 10px
  }
}
html, body {
  width: 100%;
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  background-color: #434343;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  display: flex;
  flex: 1 1;
} 

.scrollbar-container {
  width: 100%;
}
